.footer{
  background-color: var(--main-color);
  &__container{
    background-color: var(--main-color);
    padding: 20px 150px 20px 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo{
    display: block;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: -0.04em;
    text-align: left;
    color: var(--light-color);
  }
  .nav__list{
    display: flex;
    gap: 20px;
  }
  .nav__link{
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 700;
    line-height: 29px;
    letter-spacing: 0em;
    color: var(--light-color);
  }
}
@include tablet{
  .footer__container{
    background-image: url('../img/bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 20px 0px;
    flex-direction: column-reverse;
    align-items: center;
  }


  .nav__list{
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
}
