.accordion__list{
  padding-bottom: 30px;
}
.accordion{


&__control {
  text-align: left;
  width: 100%;
  background-color: transparent;
  cursor: pointer;
  font-size: 20px;
  line-height: 20px;
  color: #242424;

  }

&__content {
  font-weight: 400;
  font-size: 20px;
  color: #fff;
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0px 5px;
  will-change: max-height;
  transition: all 0.3s ease-out;
  box-sizing: content-box;
  padding-bottom: 10px;
}
&__title {
  background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: Montserrat;
  font-size: 40px;
  font-weight: 700;
  line-height: 40px;

}
&__img{
  width: 300px;
  height: 200px;
}
&__text{
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 700;
  line-height: 37px;
  letter-spacing: 0em;
  text-align: left;
  color: #2F4550;

}

&__icon {
  display: inline-block;
  width: 30px;
  height: 30px;
  fill: #fff;
  background-image: url("../img/svg/cross.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transform: rotate(45deg);
  transition: transform 0.3s ease-out;
  padding-left: 15px;
}
&:not(:first-child){
  margin-top: -1px;
}
&__btn{
  color: #FFF61D;

}
&__box{
  padding: 0px 20px;
  border: 5px solid #9587FF;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(2px);
  border-radius: 10px;
}
}

.open .accordion__icon {
  transform: rotate(135deg);
}

.open .accordion__content {
  opacity: 1;
  padding-bottom: 18px;}

 @include tablet{
  .accordion__icon{
    width: 20px;
    height: 20px;
  }
  .accordion__title{
    font-size: 20px;
  }
  .accordion__content{
    font-size: 16px;
  }
 }
