.service{
  background-color: var(--main-color);

  &__container{
    background-color: var(--main-color);
    padding: 0px 50px;
  }
  &__title{
    margin: 0;
    max-width: 768px;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 40px;
    font-weight: 700;
    line-height: 49px;
    letter-spacing: 0em;
    text-align: left;
  }
  &__list{
    margin: 0 auto;
    max-width: 1134px;
    padding-top: 20px;
  }
  &__img{
    width: 50px;
    height: 50px;
  }
  &__item{
    display: flex;
    align-items: center;
    gap: 10px;

    &:not(:last-child){
      margin-bottom: 30px;
    }
  }
  &__descr{
    margin: 0;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 500;
    line-height: 42px;
    letter-spacing: 0.04em;
    text-align: left;
    color: var(--light-color);
  }
  &__text{
    margin: 0;
    padding-top: 20px;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 50px;
    font-weight: 700;
    line-height: 61px;
    letter-spacing: 0em;
    text-align: left;
  }
}
@include tablet{
  .service__title{
    max-width: 568px;
    font-size: 30px;
    line-height: 39px;
  }
  .service__text{
    font-size: 30px;
    line-height: 39px;
  }
  .service__descr{
    font-size: 20px;
    line-height: 22px;
  }
}
@include mobile{
  .service__container{
    padding: 0px 15px 40px 15px;
  }
  .service__list{
    max-width: none;
  }
  .service__title{
    font-size: 20px;
    line-height: 29px;
  }
  .service__text{
    font-size: 20px;
    line-height: 29px;
  }
  .service__descr{
    font-size: 16px;
    line-height: 18px;
  }
}
