.send{
  background-color: var(--main-color);
  position: relative;
  margin-top: -100px;

  &__container{
    min-height: 900px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: top right;
  }
  &__title{
    margin: 0;
    padding-top: 200px;
    color: var(--light-color);
    font-family: Montserrat;
    font-size: 40px;
    font-weight: 600;
    line-height: 56px;
    letter-spacing: 0em;
    text-align: left;
  }
  &__subtitle{
    margin: 0;
    font-family: Montserrat;
    font-size: 50px;
    font-weight: 600;
    line-height: 70px;
    letter-spacing: 0em;
    text-align: left;
    color: #9587FF;
  }
  &__text{
    margin: 0;
    font-family: Montserrat;
    font-size: 34px;
    font-weight: 600;
    line-height: 48px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
    padding-bottom: 20px;
  }
  &__inp{
    padding-left: 75px;
    border-radius: 10px;
    width: 510px;
    height: 62px;
    margin-bottom: 10px;

    &::placeholder{
    font-family: Montserrat;
    color: #9587FF;
    font-size: 1.125rem;
    font-weight: 700;
    text-align: left;
    color: #9587FF;
    }
  }
  &__btn{
    margin-top: 10px;
    width: 510px;
    height: 62px;
    border: 5px solid #9587FF;
  }
  &__text-bottom{
    margin: 0;
    padding: 20px 0px 10px 10px;
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 400;
    line-height: 34px;
    letter-spacing: 0em;
    text-align: left;
    color: #F4F7FF;
  }
  &__box{
    max-width: 510px;
  }
  .control-file {
    width: 100%;
    position: relative;
    margin: 0;
    text-align: center;
  }

  #fileFF {
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }

  .input__file-icon-wrapper {
    height: 60px;
    width: 60px;
    margin-right: 15px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    border-right: 1px solid #fff;
  }

  .input__file-button-text {
    line-height: 1;
    margin-top: 1px;
  }

  .input__file-button {
    border-radius: 10px;
    width: 510px;
    height: 52px;
    background: #fff;
    color: #9587FF;
    font-size: 1.125rem;
    font-weight: 700;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    cursor: pointer;
  }
  &__icon-ws{
    padding-left: 10px;
  }
  &__icon-tl{
    padding-left: 8px;
  }
.social{
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  max-width: 216px;

  &__text{
    margin: 0;
    font-family: Montserrat;
    font-size: 12px;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: 0em;
    text-align: center;
    color: #B6D3FF;
    }
  }
}
@include tablet{
  .send__container{
    min-height: 600px;
  }
  .send__title{
    padding-top: 100px;
    font-size: 30px;
    font-weight: 600;
    line-height: 36px;
  }
  .send__subtitle{
    font-size: 25px;
    line-height: 30px;
  }
  .send__text{
    font-size: 25px;
    line-height: 30px;
  }
}


@include small-tablet{
.send{
  margin-top: 0px;
 }
}
@include mobile{
  .send__container{
    min-height: 500px;
  }
  .send__title{
    padding-top: 30px;
    font-size: 30px;
    font-weight: 600;
    line-height: 36px;
  }
  .send__subtitle{
    font-size: 20px;
    line-height: 20px;
  }
  .send__text{
    font-size: 20px;
    line-height: 20px;
  }
  .send__inp{
    width: 260px;
    height: 40px;
  }
  .input__file-button{
    width: 260px !important;
    height: 40px !important;
  }
  .send__btn{
    width: 260px;
    height: 40px;
    font-size: 16px;
    line-height: 16px;
  }
}

