.hero{
  background-color: var(--main-color);
  padding-top: 200px;


  &__container{
    position: relative;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: bottom left;
    background-color: var(--main-color);
  }
  &__inner{
    position: relative;
  }
  &__right{
    position: absolute;
    top: 20px;
    right: 0px;
    max-width: 1000px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__left{
    width: 900px;
    height: 606px;
    position: relative;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: top 100px left;
  }
  &__decor{
    position: absolute;
    bottom: -100px;
    left: 0;
  }
  &__animate{
    position: absolute;
    left: 30px;
    bottom: 10px;
    z-index: 20;
    margin: 0;
    max-width: 650px;
    font-size: 40px;
    line-height: 40px;
    color: rgb(245,245,245);
    text-shadow: 0 -1px rgba(0,0,0,.1);

    -webkit-animation: pulsate 1.2s linear infinite;
    animation: pulsate 1.2s linear infinite;
  }
  @-webkit-keyframes pulsate {
   50% { color: #FFF61D; text-shadow: 0 -1px rgba(0,0,0,.3), 0 0 5px #ffd, 0 0 8px #fff; }
  }
  @keyframes pulsate {
   50% { color: #FFF61D; text-shadow: 0 -1px rgba(0,0,0,.3), 0 0 5px #ffd, 0 0 8px #fff; }
  }

  &__title{
    margin: 0;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 90px;
    font-weight: 800;
    line-height: 126px;
    letter-spacing: 0em;
    text-align: right;
  }
  &__subtitle{
    margin: 0;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    max-width: 900px;
    padding: 30px 0px 40px 50px;
    font-family: Montserrat;
    font-size: 40px;
    font-weight: 700;
    line-height: 50px;
    letter-spacing: -0.04em;
    text-align: right;
    color: var(--light-color);
  }
  &__time{
    margin: 0;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 50px;
    font-weight: 700;
    line-height: 60px;
    letter-spacing: -0.04em;
  }

  &__btn{
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 84px;
    color: #000;
    background-color: #FFF61D;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 600;
    line-height: 37px;
    letter-spacing: 0em;
    text-align: center;
  }
  &__btn-img{
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
}
@media (max-width: 1150px){
  .hero__left{
    width: 900px;
    height: 600px;
  }
}
@media (max-width: 992px){
  .hero{
    padding-top: 100px;
  }
  .hero__container{
    padding-bottom: 60px;
  }
  .hero__left{
    width: 600px;
    height: 400px;
    position: relative;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: top 100px left;
  }
  .hero__animate{
    max-width: 300px;
    font-size: 20px;
    line-height: 20px;
  }
  .hero__decor{
    display: none !important;
  }
  .hero__title{
    font-size: 60px;
    line-height: 70px;
  }
  .hero__subtitle{
    max-width: 600px;
    padding: 10px 0px 40px 50px;
    font-family: Montserrat;
    font-size: 30px;
    line-height: 40px;
  }
  .hero__btn{
    width: 300px;
    height: 64px;
    color: #000;
    font-size: 20px;
    font-weight: 600;
    line-height: 30px;
  }
}
@media (max-width: 768px){
  .hero__left{
    width: 500px;
    height: 400px;
    background-repeat: no-repeat;
    background-position: top 90px left;
  }
  .hero__right{
    position: absolute;
    top: 20px;
    right: 5px;
  }
}
@media (max-width: 576px){
  .hero__left{
    width: 300px;
    height: 250px;
    background-repeat: no-repeat;
    background-position: top 120px left;
  }
  .hero__title{
    font-size: 40px;
    line-height: 50px;
    padding: 0px 0px 20px 0px;
  }
  .hero__subtitle{
    display: none;
  }
  .hero__btn{
    width: 180px;
    font-size: 14px;
    height: 40px;
    color: #000;
  }
}
