.adress{
  background-color: var(--main-color);

  &__container{
    padding: 10px 0px 50px 0px;
    background-color: var(--main-color);
  }
  &__title-box{
    display: flex;
    justify-content: space-around;
  }
  &__about{
    margin: 0;
    padding-bottom: 50px;
    color: var(--light-color);
    background: linear-gradient(90deg, #9587FF 0%, #F6F6F6 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 60px;
    font-weight: 800;
    line-height: 73px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  &__title{
    margin: 0;
    padding-bottom: 50px;
    color: var(--light-color);
    background: linear-gradient(90deg, #9587FF 0%, #F6F6F6 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 60px;
    font-weight: 800;
    line-height: 73px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  &__inner{
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    max-width: 1400px;
  }
  &__list{
    max-width: 410px;
    gap: 10px;
    display: grid;
    grid-template-columns: repeat(2,  1fr);
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFF61D;
    border-radius: 5px;
    border: 2px solid #9587FF;
    width: 200px;
    height: 200px;
  }
}
