.works{
  background-color: var(--main-color);

  &__container{
    background-color: var(--main-color);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  &__decor{
    position: absolute;
    top: 390px;
    left: 200px;
  }
  &__decor3{
    position: absolute;
    top: 400px;
    left: 520px;
  }
  &__decor2{
    position: absolute;
    top: 500px;
    right:450px;
  }
  &__decor4{
    position: absolute;
    top: 390px;
    right:200px;
  }
    &__title{
    margin: 0;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 90px;
    font-weight: 800;
    line-height: 126px;
    letter-spacing: 0em;
    text-align: center;
    margin-bottom: 10px;
  }
  &__subtitle{
    margin: 0;
    font-family: Montserrat;
    font-size: 26px;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: center;
    color: #FFF61D;
    padding-bottom: 40px;
  }
  &__img{
    display: block;
    margin: 0 auto;
    width: 1020px;
    height: 469px;
  }
  &__inner{
    background-image: url('../img/bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    text-align: center;
    background-color: var(--main-color);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 20px;
    border: 6px solid #9587FF80;
    width: 340px;
    height: 340px;
  }
  &__text{
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0 auto;
    padding: 60px 5px 10px 5px;
    color: #000;
    font-family: Montserrat;
    font-size: 40px;
    font-weight: 600;
    line-height: 37px;
    letter-spacing: 0em;
    text-align: center;
  }
 &__price{
   position: absolute;
   bottom: 50px;
    color: #9587FF;
    font-family: Montserrat;
    font-size: 40px;
    font-weight: 700;
    line-height: 60px;
    letter-spacing: 0em;
    text-align: center;
 }
 &__btn{
    position: absolute;
    bottom: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    font-size: 26px;
    color: #000;
    background-color:transparent;
    color:  #FFF61D;
    width: 200px;
    height: 60px;
 }
}
.swiper{
  margin-top: 50px;
  width: 1440px;
  height: 450px;
  padding: 0px 5px;

}

.swiper-pagination-bullet{
  width: 39px;
  height: 4px;
  border-radius: 2.6px;
  background-color: #9587FF;
}

.swiper-img{
  object-fit: contain;
  width: 832px;
  height: 456;
}
.swiper-btns{
  position: absolute;
  width: 100px;
  bottom: 30px;
  right: 0;
}
.swiper-button-prev{
	position: absolute;
	bottom: -55px;
	right: 0;
  background-color: #9587FF;
  width: 44px;
  height: 44px;
  background-image: url('../img/svg/prev.svg');
  background-repeat: no-repeat;
  background-position: center;

  &::after{
    display: none;
  }
}
.swiper-button-next{
	position: absolute;
	bottom: 0;
	right: 0;
  border: none;
	background-color: #9587FF;
  width: 44px;
  height: 44px;
  background-image: url('../img/svg/next.svg');
  background-repeat: no-repeat;
  background-position: center;

&::after {
  display: none;
  }
}
@media (max-width: 1460px){
  .works__decor3{
    display: none;
  }
  .works__decor2{
  display: none;
  }
  .swiper{
    width: 1080px;
  }
}
@media (max-width: 1300px){
  .works__img{
    display: block;
    width: 700px;
    height: 320px;
  }
}
@media (max-width: 1100px){
  .swiper{
    width: 720px;
  }
}
@media (max-width: 991px){
  .works__title{
    font-size: 60px;
    line-height: 76px;
    letter-spacing: 0em;
  }
  .works__decor{
    display: none;
  }
  .works__decor4{
    display: none;
  }
}

@media (max-width: 768px){
  .works__img{
    display: block;
    width: 500px;
    height: 220px;
  }
  .swiper{
    width: 360px;
  }
  .swiper-btns{
    position: absolute;
    width: 75px;
    bottom: 65px;
    right: 10px;
  }
  .swiper-button-prev{
    position: absolute;
    bottom: -55px;
    right: 0;
    background-color: #9587FF;
    width: 30px;
    height: 30px;
    background-image: url('../img/svg/prev.svg');
    background-repeat: no-repeat;
    background-position: center;

    &::after{
      display: none;
    }
  }
  .swiper-button-next{
    position: absolute;
    bottom: 0;
    right: 0;
    border: none;
    background-color: #9587FF;
    width: 30px;
    height: 30px;
    background-image: url('../img/svg/next.svg');
    background-repeat: no-repeat;
    background-position: center;

  &::after {
    display: none;
    }
  }
  .swiper-pagination-bullet{
    width: 26px;
    height: 4px;
    border-radius: 2.6px;
    background-color: #9587FF;
  }
}
@media (max-width: 576px){
  .works__title{
    font-size: 40px;
    line-height: 40px;
    margin-bottom: 10px;
  }
  .works__subtitle{
    font-size: 18px;
    line-height: 18px;
  }
  .works__img{
    display: block;
    width: 300px;
    height: 130px;
  }
  .swiper{
    width: 300px;
  }
  .works__inner{
    border-radius: 20px;
    border: 6px solid #9587FF80;
    width: 280px;
    height: 320px;
  }
  .works__text{
    padding: 10px;
    padding-top: 20px ;
    color: #000;
    font-family: Montserrat;
    font-size: 26px;
  }
  .swiper-btns{
    position: absolute;
    width: 75px;
    bottom: 85px;
    right: 10px;
  }
}


