.aboutus{
  background-color: var(--main-color);
  padding-top: 50px;

  &__title{
    margin: 0;
    padding: 50px 0px 50px 50px;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 62px;
    font-weight: 800;
    line-height: 63px;
    letter-spacing: 0em;
    text-align: left;
  }
  &__subtitle{
    margin: 0;
    padding: 0px 0px 50px 50px;
    max-width: 1359px;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 40px;
    font-weight: 600;
    line-height: 47px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  &__descr{
    margin: 0;
    padding: 0px 0px 50px 100px;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    max-width: 1259px;
    font-family: Montserrat;
    font-size: 40px;
    font-weight: 600;
    line-height: 47px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  &__inner{

    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  &__subtitle{
    margin: 0;
    padding: 20px 0px 0px 70px;
    font-size: 60px;
    line-height: 60px;
    font-weight: 700;
    letter-spacing: 0em;
    text-align: left;
    color: #F4F7FF;
  }
  // &__inner{
  //   min-height: 520px;
  // }
  &__list-inner{
    max-width: 1200px;
    display: flex;
    justify-content: space-around;
    padding: 0px 50px 50px 70px;
  }
  &__inner-text{
    font-family: Montserrat;
    font-size: 26px;
    font-weight: 600;
    line-height: 40px;
    letter-spacing: 0em;
    text-align: left;
    color: #000;
    margin-right: 20px;
  }
}

@media  (max-width: 1440px){
  .aboutus__subtitle{
    padding: 0px 0px 20px 20px;

  }

  .aboutus__list-inner{
    padding: 0px 20px 20px 20px;
  }
  .aboutus__descr{
    padding: 0px 0px 20px 60px;
    max-width: 1000px;
    font-size: 30px;
    line-height: 37px;
  }
}

@media  (max-width: 991px){

  .aboutus__subtitle{
    padding: 20px 0px 0px 20px;
    font-size: 40px;
    line-height: 60px;
  }
  .aboutus__inner-text{
    font-size: 20px;
    line-height: 30px;
  }
}
@media  (max-width: 767px){
  .aboutus__list-inner{
    flex-direction: column;
    align-items: center;
  }
}
