.time{
  background-color: var(--main-color);
  &__container{
    background-color: var(--main-color);
    padding-bottom: 130px;
  }
  &__list{
    display: flex;
    justify-content: space-around;

  }
  &__item{
    position: relative;
  }
  &__title{
    margin: 0;
    padding-top: 50px;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 100px;
    font-weight: 800;
    line-height: 140px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  &__text{
    margin: 0;
    position: absolute;
    top: 120px;
    left: 20px;
    font-family: Montserrat;
    font-size: 160px;
    font-weight: 800;
    line-height: 224px;
    letter-spacing: 0em;
    text-align: left;
    color: #9587FF;

    &-center{
      left: 60px;
    }
  }
  &__vertical{
    position: absolute;
    top:170px;
    left:-60px;
    writing-mode: vertical-lr;
    text-orientation: upright;
    font-family: Montserrat;
    font-size: 60px;
    font-weight: 800;
    line-height: -20px;
    letter-spacing: 0em;
    color: #9587FF;

    &-top{
      top: 150px;
    }
    &-right{
      top: 200px;
    }

    &-center{
      top: 220px;
    }
    &-bottom{
      top: 250px;
    }
  }
  &__list-bottom{
    margin-top: 120px;
    display: flex;
    justify-content: space-between;

  }
  &__text-bottom{
      margin: 0;
      max-width: 449px;
      font-family: Montserrat;
      font-size: 24px;
      font-weight: 600;
      line-height: 29px;
      letter-spacing: -0.02em;
      text-align: left;
      color: var(--light-color);
      margin-left: 50px;

      &-midle{
        max-width: 392px;
        text-align: center;
      }
      &-right{
        text-align: center;
      }

      span{
        color: #FFF61D;
      }

  }
}
@include big-desktop {
  .time__container{
    padding-bottom: 50px;
  }
  .time__list{
    min-height: none;
  }
  .time__title{
    margin: 0;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    padding: 20px 0px 30px 0px;
    font-size: 60px;
    font-weight: 800;
    line-height: 98px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
    text-transform: uppercase;
  }
  .time__vertical-title{
    margin: 0;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    padding-top: 30px;
    font-size: 50px;
    line-height: 80px;
    font-weight: 800;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  .time__text{
    margin: 0;
    position: absolute;
    top:0px;
    left: 20px;
    font-family: Montserrat;
    font-size: 70px;
    font-weight: 800;
    line-height: 254px;
    letter-spacing: 0em;
    text-align: left;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;

    &-center{
      left: 50px;
    }
  }
  .time__vertical{
    position: absolute;
    top:100px;
    left:-10px;
    writing-mode: vertical-lr;
    text-orientation: upright;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 700;
    line-height: -20px;
    letter-spacing: 0em;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;

    &-top{
      top: 90px;
    }
    &-right{
      top: 115px;
    }

    &-center{
      top: 130px;
    }
    &-bottom{
      top: 140px;
    }

  }
  .time__list-bottom{
  display: none;
  }

}
