.list{
  background-color: var(--main-color);

  &__container{
    position: relative;
    min-height: 1000px;
    background-color: var(--main-color);
    background-repeat: no-repeat;
    background-position: top left;
    background-size: contain;
  }
  &__top{
   display: flex;
   justify-content: flex-end;
  }
  &__list{
    max-width: 490px;
    padding-bottom: 50px;
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
  }
  &__descr{
    margin: 0;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 600;
    line-height: 42px;
    letter-spacing: 0.04em;
    text-align: right;

    &-left{
      text-align: right;
    }
  }
  &__descr-white{
    margin: 0;
    color: #fff;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 600;
    line-height: 42px;
    letter-spacing: 0.04em;
    text-align: left;
 }
}
@include tablet{
  .list__container{
    min-height: 700px;
  }
  .list__list{
    padding-bottom: 20px;
  }
  .list__descr{
    font-size: 20px;
    font-weight: 600;
    line-height: 32px;
  }
  .list__descr-white{
    font-size: 20px;
    font-weight: 600;
    line-height: 32px;
  }
}
@include small-tablet{
  .list__container{
    min-height: 500px;
  }
}
@include mobile{
  .list__container{
    min-height: 400px;
  }
  .list__descr-white{
    display: none;
  }
}
