.header {

    position: absolute;
    width: 100%;
    z-index: 100;
    top: 0;
    left: 0;


  &__container{
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__inner{
    display: flex;
    gap: 15px;
  }
	&__logo {
    display: block;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: -0.04em;
    text-align: left;
    color: var(--light-color);
	}
  &__phone-box{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }
  &__phone{
    font-family: Montserrat;
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  &__text{
    margin: 0;
    position: absolute;
    left: 0px;
    top: 30px;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFF61D;
  }
  &__sotial{
    width: 40px;
    height: 40px;
  }
}

.menu {
	&__icon {
		display: none;
		@media (max-width: 992px) {
			display: block;
			width: 30px;
			height: 18px;
			position: relative;
			z-index: 5;
			cursor: pointer;
			span {
				position: absolute;
				top: calc(50% - 1px);
				left: 0;
				width: 100%;
				height: 2px;
				background-color: #FFF61D !important;
				transition: all 0.2s;
				&:first-child {
					top: 0;
				}
				&:last-child {
					top: auto;
					bottom: 0;
				}
			}
			&.active {
				span {
					transform: scale(0);
					&:first-child {
						transform: rotate(-45deg);
						top: calc(50% - 1px);
					}
					&:last-child {
						transform: rotate(45deg);
						bottom: calc(50% - 1px);
					}
				}
			}
		}
	}
	&__body {
		@media (max-width: 992px) {
			position: fixed;
			top: -15px;
			right:-100%;
			height: 100vh;
			width: 100%;
			transition: all 0.2s;
      z-index: 2;
		}
		&.active {
			right: 0;
		}
	}

	&__list {
		list-style-type: none;
		display: flex;

		gap: 30px;
		@media (max-width: 992px) {
			flex-direction: column;
			padding: 30px;
			gap: 20px;
			height: 100%;
			width: 100%;
      background-color: var(--main-color);
      background-image: url('../img/bg.png');
      background-repeat: no-repeat;
      background-size: cover;
      background-repeat: no-repeat;
			overflow: auto;
		}
	}
	&__link {
  font-family: Montserrat;
  font-size: 24px;
  font-weight: 700;
  line-height: 29px;
  letter-spacing: 0em;
  color: var(--light-color);


		@media (max-width: 768px) {
			display: block;
			font-size: 30px;
			line-height: 70px;
		}
	}
}

@include big-desktop{
  .header__logo{
    padding-left: 10px;
  }
  .header__text{
    left: 10px;
    top: 30px;
  }
}
@include small-tablet{

  .header__phone-box{
    gap: 10px;
  }
  .header__logo{
    font-size: 20px;
    line-height: 30px;
    padding-left: 0;
  }
  .header__text{
    left: 0px;
    top: 20px;
    font-size: 12px;
    line-height: 27px;
  }
}
