.pricehero{
  background-color: var(--main-color);
  padding-top: 100px;

  &__container{
    position: relative;
    padding-top: 100px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }
  &__title{
    margin: 0;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    padding: 30px 0px 40px 0px;
    font-family: Montserrat;
    font-size: 80px;
    font-weight: 700;
    line-height: 90px;
    letter-spacing: -0.04em;
    text-align: left;
    color: var(--light-color);
  }
  &__subtitle{
    margin: 0;
    font-family: Montserrat;
    font-size: 36px;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFF61D;
    padding-bottom: 10px;

  }
}

@include tablet{
  .pricehero__title{
    font-size: 60px;
    line-height: 70px;
    padding: 20px 0px;
  }
  .pricehero__subtitle{
    font-size: 26px;
    line-height: 36px;
  }
}
