.about{
  background-color: var(--main-color);

  &__container{
    padding: 50px;
    background-color: var(--main-color);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    min-height: 600px;
  }
  &__title{
    margin: 0;
    padding: 150px 0px 50px 30px;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 48px;
    font-weight: 800;
    line-height: 59px;
    letter-spacing: 0em;
    text-align: left;
    text-transform: uppercase;
  }
  &__list{
    margin: 0 auto;
    max-width: 1400px;
    display: flex;
    gap: 30px;
  }
  &__item{
    position: relative;
    width: 430px;
    height: 450px;
  }

  &__descr{
    margin: 0;
    padding: 20px 0px;
    font-family: Montserrat;
    font-size: 32px;
    font-weight: 600;
    line-height: 44px;
    letter-spacing: 0em;
    text-align: left;
    color: #F4F7FF;
  }
  &__text{
    padding-top: 20px;
    margin: 0;
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 500;
    line-height: 34px;
    letter-spacing: 0em;
    text-align: left;
    color: #F4F7FF;
  }
  &__inner{
    margin-top: 100px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  &__subtitle{
    margin: 0;
    padding: 20px 0px 0px 70px;
    font-size: 60px;
    line-height: 60px;
    font-weight: 700;
    letter-spacing: 0em;
    text-align: left;
    color: #F4F7FF;
  }
  &__inner{
    min-height: 520px;
  }
  &__list-inner{
    max-width: 1200px;
    display: flex;
    justify-content: space-around;
    padding: 0px 50px 50px 70px;
  }
  &__inner-text{
    font-family: Montserrat;
    font-size: 26px;
    font-weight: 600;
    line-height: 40px;
    letter-spacing: 0em;
    text-align: left;
    color: #000;
    margin-right: 20px;
  }
}
@include big-desktop {
  .about__title{
    padding: 30px 0px 50px 0px;
  }
  .about__list-inner{
    padding: 0px 20px 20px 20px;
  }
}
@include tablet{
  .about__container{
    padding: 0px;
  }
  .about__title{
    font-size: 30px;
    padding: 10px 0px 20px 20px;
  }
   .about__list{
    flex-direction: column;
  }
  .about__item{
    width: 400px;
    height: 300px;
  }
  .about__img{
    margin-left: 20px;
    width: 100px;
    height: 100px;
  }
  .about__subtitle{
    padding: 20px 0px 0px 20px;
    font-size: 40px;
    line-height: 60px;
  }
  .about__inner-text{
    font-size: 20px;
    line-height: 30px;
  }
  .about__descr{
    padding: 10px 20px;
    font-size: 26px;
    line-height: 36px;
  }
  .about__text{
    padding: 20px 0px 20px 20px;
    font-size: 20px;
    line-height: 34px;
  }
}

@include mobile{
  .about__item{
    max-width: 300px;
    height: 380px;
  }
  .about__title{
    line-height: 30px;
  }
  .about__list-inner{
    flex-direction: column;
    align-items: center;
  }

}
