.swipe{
  background-color: var(--main-color);

  &__title{
    margin: 0;
    padding: 30px 0px 60px 0px;
    font-family: Montserrat;
    font-size: 55px;
    font-weight: 800;
    line-height: 67px;
    letter-spacing: 0em;
    text-align: left;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }


  &__container{
    overflow: hidden;
  }

  .corusel{
    position: relative;
    width: 1550px;
    height: 800px;

    &__top{
      display: flex;
      gap: 40px;
    }
    &__bottom{
      position: absolute;
      top: 320px;
      left: 80px;
      display: flex;
      gap: 40px;
    }

    &__img{
      width: 390px;
      height: 390px;
    }
    &__inner{
      position: relative;
      display: flex;
      height: 800px;
    }
    &__text{
      margin: 0;
      padding: 0px 0px 0px 60px;
      max-width: 460px;
      font-family: Montserrat;
      font-size: 24px;
      font-weight: 500;
      line-height: 29px;
      letter-spacing: 0em;
      text-align: left;
      color: var(--light-color);

      span{
      display: block;
      padding-bottom: 10px;
       color: #FFF61D;
      font-size: 24px;
      font-weight: 700;
      line-height: 29px;
      letter-spacing: 0em;
      text-align: left;

      }
    }
  }
  .corusel-btns{
    position: absolute;
    width: 100px;
    bottom: 0px;
    right: 30px;
  }
  .corusel-button-prev{
    position: absolute;
    bottom: 0px;
    right: 240px;
    background-color: #9587FF;
    width: 50px;
    height: 50px;
    background-image: url('../img/svg/next.svg');
    background-repeat: no-repeat;
    background-position: center;
    z-index: 100;

    &::after{
      display: none;
    }
  }
  .corusel-button-next{
    position: absolute;
    z-index: 100;
    bottom: 0;
    right: 300px;
    border: none;
    background-color: #9587FF;
    width: 50px;
    height: 50px;
    background-image: url('../img/svg/prev.svg');
    background-repeat: no-repeat;
    background-position: center;

  &::after {
    display: none;
    }
  }

}
