
.graph-modal__close{
  background-repeat: no-repeat;
  font-weight: 400;
  font-size: 18px;
  line-height: 30%;
  width: auto;
  height: auto;
  right: 20px;
  top: 25px;
  color: var(--light-color);
  background-image: none;

}

.popup {
   padding: 20px 10px;
   background-color: var(--main-color);
   max-width: 1240px;

   @include small-tablet {
        padding-top: 80px;
      }
   @include  mobile{
    padding: 50px 0px 10px 0px;
   }

&__top {
  display: flex;
  width: 100%;
  min-height: 350px;
  margin-bottom: 10px;
}
&__img{
  object-fit: cover;
  width: 400px;
  height: 300px;
}

&__about{
  margin-left: 40px;
}
&__name {
  font-family: Montserrat;
  margin: 0;
  background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  padding: 20px 0px 0px 30px;
  font-weight: 600;
  font-size: 60px;
  line-height: 70px;
}

&__post {
  font-family: Montserrat;
  margin: 0;
  background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  padding-left: 30px;
  font-weight: 400;
  font-size: 30px;
  line-height: 35px;
  margin-bottom: 10px;
}
&__descr{
  margin: 0;
  background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: Montserrat;
  font-weight: 400;
  font-size: 20px;
  line-height: 25px;
  color: var(--light-color);
  padding-bottom: 15px;

  &-small{
    padding-left: 20px;
    font-size: 20px;
    line-height: 30px;
  }
  &-big{
    font-size: 40px;
    line-height: 40px;
    padding-bottom: 20px;
  }
  span{
    color: #FFCF26;
    padding-left: 10px;
  }
}

&__price{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  font-family: Montserrat;
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: var(--light-color);
  margin-bottom: 10px;

  span{
    padding-left: 20px;
    display: inline-block;
    color: #FFCF26;
  }
}

