&__text {
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 160%;
  color: var(--light-color);
  @include mobile{
    font-size: 16px;
    line-height: 130%;
  }
}

&__subtitle {
  font-weight: 600;
  font-size: 24px;
  line-height: 160%;
}

.simplebar-track.simplebar-vertical{
  margin: 20px 0px;
  border-radius: 40px;
  background:#dedce2;
  width: 5px;
}
.simplebar-scrollbar::before{
  left: 0;
  right: 0;
  opacity: 1;
  width: 5;
  border-radius: 40px;
  background:#FFCF26;
}

}
@include tablet{
  .popup{
    padding: 25px 10px 20px 10px;
  }
  .popup__about{
    margin: 0;
  }
  .popup__top{
    flex-direction: column;
    min-height: 300px;
  }
  .popup__name{
    padding:10px 0px 0px 0px;
    font-size: 25px;
    line-height: 35px;
  }
  .popup__post{
    padding-left: 0px;
    font-size: 18px;
    line-height: 20px;
  }
  .popup__img{
    width: 300px !important;
    height: 250px !important;
  }
  .popup__descr{
    font-size: 16px;
    line-height: 20px;
    padding-bottom: 10px;

    &-small{
      padding-left: 20px;
      font-size: 20px;
      line-height: 30px;
    }
    &-big{
      font-size: 26px;
      line-height: 30px;
      padding-bottom: 20px;
    }
    span{
      color: #FFCF26;
      padding-left: 10px;
    }
  }
  .popup__price{
    font-size: 16px;
    line-height: 26px;
    margin-bottom: 0px;
  }
}
