.contacts{
  background-color: var(--main-color);
  &__container{
    background-color: var(--main-color);
    padding: 0px 0px 100px 0px;
  }
  &__title{
    margin: 0;
    padding: 30px 0px 50px 0px;
    color: var(--light-color);
    background: linear-gradient(90deg, #9587FF 0%, #F6F6F6 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 60px;
    font-weight: 800;
    line-height: 73px;
    letter-spacing: 0em;
    text-align: center;
    color: var(--light-color);
  }
  &__inner{
    display: flex;
    justify-content: space-around;
    gap: 50px;
    padding: 0px 50px;
  }
  &__item{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    justify-content: flex-start;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  &__text{
    margin: 0;
    max-width: 500px;
    font-family: Montserrat;
    font-size: 26px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);

    span{
      font-size: 20px;
      line-height: 26px;
      font-weight: 500;
    }
  }
  &__phone{
    display: flex;
    align-items: center;
    gap: 20px;
    font-family: Montserrat;
    font-size: 26px;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
    margin-bottom: 10px;
  }
  &__icon{
    display: block;
    width: 40px !important;
    height: 40px !important;
    fill: #9587FF;

    &-location{
      height: 38px;
    }
  }
  &__phone-icon{
    width: 40px;
    height: 40px;
  }
  // &__sotial-title{
  //   margin: 0;
  //   padding: 80px 0px 30px 0px;
  //   font-family: Montserrat;
  //   font-size: 40px;
  //   font-weight: 700;
  //   line-height: 56px;
  //   letter-spacing: 0em;
  //   text-align: left;
  //   color: var(--light-color);
  // }
  // &__social{
  //   display: flex;
  //   justify-content: center;
  //   gap: 20px;
  // }
 &__box{
    border-radius: 20px;
    border: 2px solid #9587FF;
    width: 590px;
    height: 633px;
 }
 &__form-title{
    margin: 0;
    padding-top: 20px;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 55px;
    font-weight: 800;
    line-height: 67px;
    letter-spacing: -0.02em;
    text-align: center;
 }
 &__form-subtitle{
    margin: 0;
    padding-bottom: 50px;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 600;
    line-height: 37px;
    letter-spacing: 0em;
    text-align: center;
    color: #F4F7FF;
 }
 &__form-text{
    margin: 0;
    font-family: Montserrat;
    font-size: 21px;
    font-weight: 500;
    line-height: 29px;
    letter-spacing: 0em;
    text-align: center;
    color: var(--light-color);
 }
 &__form{
  margin: 0 auto;
  max-width: 510px;
 }
 &__input{
  outline: none;
  width: 510px;
  height: 72px;
  padding-left: 20px;
  border-radius: 10px;
  margin-bottom: 20px;

  &::placeholder{
    font-family: Montserrat;
    font-size: 26px;
    line-height: 40px;
    color: #9587FF;
  }
 }
 &__btn{
  width: 510px;
  height: 72px;
  border-radius: 50px;
  background-color: #FFF61D;
  color: #000;
  margin-bottom: 20px;
 }
 &__icon{
  width: 50px;
  height: 50px;
 }
 &__img-box{
  max-width: 500px;
  margin-top: 10px;
  position: relative;
 }
 &__img{
  border-radius: 10px;
  width: 500px;
  height: 230px;

  &-small{
    border-radius: 10px;
    width: 70px;
    height: 70px;
  }
 }
}
@include big-desktop{
  .contacts__inner{
    flex-wrap: wrap;
  }
}

@include tablet{
  .contacts__title{
    font-size: 40px;
    line-height: 50px;
  }
  .contacts__map{
    width: 700px;
  }
}

@include small-tablet{
  .contacts__container{
    padding-bottom: 20px;
  }
  .contacts__inner{
   padding: 0px 10px;
  }
  .contacts__map{
    width: 500px;
    height: 400px;
  }
  .contacts__sotial-title{
    padding: 30px 0px;
    font-size: 30px;
    line-height: 40px;
  }
  .contacts__text{
    font-size: 20px;
    line-height: 32px;

    span{
      font-size: 16px;
    }
  }
  .contacts__phone{
    font-size: 20px;
    line-height: 30px;
  }
  .contacts__icon{
    display: block;
    width: 30px;
    height: 30px;

  }
}
@include mobile{
  .contacts__sotial{
    display: block;
  }
  .contacts__box{
    width: 310px;
    padding: 5px 15px;
    height: 360px;
  }
  .contacts__input{
    width: 100%;
    height: 40px;

    &::placeholder{
      font-size: 18px;
      line-height: 20px;
    }
  }
  .contacts__form-title{
    padding-top: 10px;
    font-size: 30px;
    line-height: 37px;
  }
  .contacts__form-subtitle{
    font-size: 20px;
    line-height: 37px;
    padding-bottom: 10px;
  }
  .contacts__btn{
    width: 100%;
    height: 40px;
    font-size: 18px;
    margin-bottom: 10px;
  }
  .contacts__form-text{
    font-size: 14px;
    line-height: 15px;

  }
}

