.paint{
  background-color: var(--main-color);
  &__container{
    min-height: 847px;
    background-color: var(--main-color);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: top  right;
  }
  &__inner{
    position: relative;
  }
  &__title{
    margin: 0;
    padding-top: 150px;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Montserrat;
    font-size: 70px;
    font-weight: 800;
    line-height: 90px;
    letter-spacing: 0em;
    text-align: left;
    padding-left: 50px;

  }
  &__subtitle{
    margin: 0;
    background: linear-gradient(90deg, #F4F7FF 0%, #9587FF 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    max-width: 900px;
    padding: 30px 0px 10px 50px;
    font-family: Montserrat;
    font-size: 55px;
    font-weight: 700;
    line-height: 65px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  &__descr{
    margin: 0;
    max-width: 662px;
    padding: 10px 0px 30px 50px;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 500;
    line-height: 43px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  &__btn{
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 350px;
    height: 60px;
    color: #000;
    background-color: #FFF61D;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 600;
    line-height: 37px;
    letter-spacing: 0em;
    text-align: center;
    margin: 20px 0px 0px 50px;
  }
  &__btn-img{
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
}

@include tablet{
  .paint__container{
    min-height: 700px;
  }
  .paint__title{
    max-width: 600px;
    padding: 120px 0px 20px 20px;
    font-family: Montserrat;
    font-size: 50px;
    line-height: 61px;
    letter-spacing: 0em;
  }
  .paint__subtitle{
    max-width: none;
    padding: 0px 0px 20px 20px;
    font-size: 45px;
    line-height: 55px;
    letter-spacing: 0em;
  }
  .paint__descr{
    max-width: 462px;
    padding: 20px 0px 30px 20px;
    font-family: Montserrat;
    font-size: 26px;
    font-weight: 500;
    line-height: 43px;
  }
  .paint__btn{
    width: 300px;
    height: 64px;
    margin: 0px;
    font-size: 20px;
    font-weight: 600;
    line-height: 30px;
  }
}
@include mobile{
  .paint__container{
    min-height: 400px;
  }
  .paint__title{
    max-width: 400px;
    padding: 80px 0px 10px 0px;
    font-family: Montserrat;
    font-size: 35px;
    line-height: 50px;
    letter-spacing: 0em;
  }
  .paint__subtitle{
    max-width: none;
    padding: 0px 0px 10px 0px;
    font-size: 25px;
    line-height: 35px;
  }
  .paint__descr{
    max-width: 462px;
    padding: 0px 0px 10px 0px;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 500;
    line-height: 28px;
  }
  .paint__btn{
    width: 200px;
    height: 40px;
    margin: 0px;
    font-size: 16px;
    line-height: 20px;
  }
}
